.header-display {
    display: flex;
    flex-direction: row;
    background: $bg-color;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    .input-header {
        background-image: url(http://www.clker.com/cliparts/D/k/0/U/i/k/search-icon-red-md.png);
        background-repeat: no-repeat;
        background-size: 20px 20px;
        background-position: 10px 14px;
        padding-left: 40px;
    }
    img {
        width: 50px;
        height: auto;
    }
    i {
        font-size: 25px;
        color: $bg-color-purple;
    }
    @include mq($until: mobile) {
        margin-right: auto;
        margin-left: auto;
        padding: 10px 8px;
        .cauldron-bag {
            display: flex;
            flex-flow: column;
            text-align: center;
        }
        img {
            width: 40px;
            height: auto;
        }
    }

}

.input-display {
    border-radius: 5px;
    border: 1px solid $color-border;
    color: $color-dark;
    outline: none;
}

.cauldron-bag {
    display: flex;
    flex-flow: row;
    align-items: center;
}

.purple-text {
    color: rgb(101, 70, 151);
    font-size: 0.60em;
    font-family: 'Bad Script', cursive;
    position: relative;
    &::before {
    content: "";
    border: 1px solid $color-transparent;
    position: absolute;
    width: 25%;
    top: 16px;
    left: -55px;
}   &::after {
    content: "";
    border: 1px solid $color-transparent;
    width: 25%;
    position: absolute;
    top: 16px;
    right: -55px;
    }
    @include mq($until: mobile) {
    &::before {
    content: "";
    border: 1px solid $color-transparent;
    position: absolute;
    width: 25%;
    top: 10px;
    left: -35px;
}   &::after {
    content: "";
    border: 1px solid $color-transparent;
    width: 25%;
    position: absolute;
    top: 10px;
    right: -35px;
    }    
}
}