.black_overlay{
    display: none;
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background-color: black;
    z-index:1001;
    -moz-opacity: 0.8;
    opacity:.80;
    filter: alpha(opacity=80);
}
.white_content {
    display: none;
    position: fixed;
    top: 10%;
    left: 15%;
    width: 70%;
    height: 80%;
    padding: 50px;
    border: 1px solid black;
    background-color: white;
    z-index:1002;
    overflow: auto;
    @if $display {
        flex-flow: row
    }
    button {
        position: absolute;
        top: 1%;
        right: 1%;
        font-size: 20px;  
        font-style: inherit;
        border: none;
        outline: none;
        color: black;
        padding: 10px;
        background-color: inherit;
        font-family: inherit;
        margin: 0;
        cursor: pointer;
    }
}

.details {
    flex-flow: column
}
