/* ==========================================================================
   #VISIBILITY
   ========================================================================== */

.u-hidden-visually {
  @include hidden-visually;
}

/**
 * Hide visually and from screen readers.
 */

.u-hidden {
  display: none !important;
}

@each $name, $value in $breakpoints {
  .u-hidden–#{$name} {
    @include mq($from: $name) {
      display: none !important;
    }
  }
}
