.nav-display {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: $bg-color-purple;
    color: #FFF;
    font-style: italic;
    font-size: 1em;
    font-family: 'Times New Roman', Times, serif;
    i {
        padding-left: 10px;
    } 
    button {
    font-size: unset;  
    font-style: inherit;
    border: none;
    outline: none;
    color: white;
    padding: 22px 18px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
  }@include mq($until: mobile) {
    background-color: $color-grey;
    color: $color-text;
    width: 100vw;
    padding: 5px 0px;
    span {
        position: relative;
        font-size: 0.7em;
        margin-left: auto;
        margin-right: auto;
    }
}
  
}
.nav-items{
    flex-grow: 2;
    flex-wrap: nowrap;
    overflow: hidden;
    .relative {
        position: relative;
    }
    button {
        border-radius: 7px;
    }
    button:hover {
        background-color: #422a53;
    }
}
.nav-user {
    flex-grow: 1;
    a {
        padding: 22px;
    }
    a:hover {
        border-radius: 7px;
        background-color: #422a53; 
    }
}

//--------------DROPDOWN MENU------------\/
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.8);
    z-index: 1;
}

.dropdown-content p {
    color: black;
    padding: 12px 16px;
    display: block;
    text-align: left;
}

.dropdown-content p:hover {
    background-color: #c4c4c4;
}

.dropbtn:hover .dropdown-content {
    display: block;
}

#hover-fixed {
    background-color: #422a53;
}
//-------------DROPDOWN MENU-------------/\