.footer-display {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: $bg-color-gray;
    justify-content: space-between;
    .utility-links {
        display: flex;
        flex-wrap: wrap;
        max-width: 400px;
        & p {
            margin-top: 10px;
            margin-right: 15px;
        }
    }
    .icons {
        color: $bg-color-purple;
        font-size: 35px;
        justify-content: space-between;
        padding: 25px 0px;
        & i {
            margin-right: 50px;
        }
        @include mq($until: mobile) {
            font-size: 25px;
            text-align: center;
            padding: 15px 0px;
            width: 100%;
            i{
                margin: 0px 14px;
            }
        }
    }
    .copyright {
        padding-top: 30px;
        color: $color-text;
        font-size: 1.3em;
        @include mq($until: mobile) {
            font-size: 1em;
            padding: 5px 0px 30px 0px;
        }
    }
    .newsletter {
        display: flex;
        flex-wrap: wrap;
        padding: 35px 30px;
        background-color: #FFF;
        border-radius: 7px;
        width: 450px;
        height: 200px;
        @include mq($until: mobile) {
            width: 100vw;
            height: auto;
            margin: 10px;
            padding: 20px 10px;
            border-radius: 0px;
            font-size: 0.6em;
            display: block;
        }
        .submit{
            @include mq($until: mobile) {
                display: flex;
                flex-flow: row;
                margin-top: 10px;
            }
        }
        button {
            font-size: unset;  
            font-style: inherit;
            font-size: 1.1em;
            border: none;
            color: white;
            background-color: $color-red;
            font-family: inherit;
            margin: 0;
            border-radius: 7px;
            height: 48px;
            width: 90px;
            text-align: center;
            @include mq($until: mobile) {
                width: 20vw;
                height: 25px;
                font-size: 1em;
                border-radius: 4px;
            }
        }
        input {
            padding-left: 10px;
            margin-right: 10px;
            @include mq($until: mobile) {
                width: 60vw;
                height: 25px;
            }            
        }
        p {
            font-size: 1.1em;
            @include mq($until: mobile) {
                display: block;
                margin: 5px 0px;
            }
        }
        @include mq($until: mobile) {
            width: 100vw;
        }
    }
    @include mq($until: mobile) {
        flex-wrap: wrap-reverse;
    }
}