.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 2; 
    top: 0;
    left: 0;
    background-color: #fff;
    overflow-x: hidden; 
    transition: 0.5s;
}

.sidenav a{
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 15px;
    color: #fff;
    display: block;
    &:not(:first-child) {
        background-color: $bg-color-purple;
        transition: 0.3s;
    }
}

.sidenav a:hover:not(:first-child) {
    background-color: #422a53;
}

.sidenav .closebtn {
    color: #422a53;
    padding: 5px 5px;
    font-size: 30px;
    cursor: pointer;
}

.user-mobile {
    text-align: center;
    padding: 10px 0px;
    background-color: $bg-color-purple;
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    a {
        padding: 8px;
    }
    a:hover {
        background-color: #422a53;
    }
}

.menu-header {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly;
    padding: 15px 5px;
    align-items: center;
    input {
        width: 70%;
    }
}

.input-header {
    background-image: url(http://www.clker.com/cliparts/D/k/0/U/i/k/search-icon-red-md.png);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: 10px 14px;
    padding-left: 40px;
}