html {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

header {
  height: 10em;
  @include mq($until: mobile) {
    height: unset;
    h1 {
      margin: 0px;
    }
}
}

nav {
  max-height: 5em;
}

footer {
  min-height: 22em;
  @include mq($until: mobile) {
    min-height: 5em;
}
}

input {
  height: 3em;
  width: 18em;
}

img {
  height: 250px;
  width: auto;
  @include mq($until: mobile) {
    height: 130px;
    width: auto;
  }
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}
