@include placeholder-color;

// ------------- FONTS STYLE --------------\/
.fontFamily-TNR {
    font-family: 'Times New Roman', Times, serif;
}

.fontHeight-small {
    line-height: 0.8em;
}

.fontSize-merlin {
    font-size: 2em;
    @include mq($until: mobile) {
        font-size: 1.4em;
    }
    
}

.fontSize-big {
    font-size: 1.4em;
    @include mq($until: mobile) {
        font-size: 0.8em;
    }
}

.fontSize-small {
    font-size: 0.75em;
}

.fontStyle-italic {
    font-style: italic;
}

.red-text {
    color: $color-red
}

.lightGray-text {
    color: $color-transparent;
}

.fontweight-bold {
    font-weight: 700;
}

.fontweigth-thin {
    font-weight: 300;
}

.pointer {
    cursor: pointer;
}

.mobile-hidden {
    @include mq($until: mobile) {
        @include hidden-visually
    }
}

.laptop-hidden {
    @include mq($from: mobile) {
        @include hidden-visually
    }
}