.potions-display {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
    font-size: 0.6em;
    max-width: 1132px;
    margin-left: auto;
    margin-right: auto;
    @include mq($until: mobile) {
        width: 100vw;
        font-size: 0.35em;
        justify-content: space-around;
    }
}

.Potion {
    padding: 50px 20px;
    cursor: pointer;
    @include mq($until: mobile) {
        padding: 20px 10px;
    }
}