/* ==========================================================================
   #CONTAINER-OBJECT
   ========================================================================== */

.header-container {
  padding: 10px 50px;
  @include mq($until: mobile) {
    padding: 15px 10px;
}
}

.nav-container {
  padding: 12px 50px;
  @include mq($until: mobile) {
    padding: 8px 0px;
  }
}

.footer-container {
  padding: 35px 40px;
  @include mq($until: mobile) {
   padding: 5px;
}  
}

.potions-container {
  padding: 65px 60px;
  width: 100vw;
  align-content: center;
  margin-right: auto;
  margin-left: auto;
  @include mq($until: mobile) {
    width: 100%;
    padding: 20px 0px;
    margin: 0px;
    text-align: center;
    font-size: 1.8em;
}
}
